import { useEffect, useState } from "react"
import Movie from './Movie'
import '../App.css'

const Movies = () => {
    const [movieList, setMovieList] = useState([]);
    const [error, setError] = useState(null);

    useEffect(() => {
        fetch(
            `https://api.themoviedb.org/4/list/7104454?page=1&api_key=3616c1f7e1ad527fc93d8cf2eda4a737`
        )
            .then((response) => response.json())
            .then((data) => setMovieList(data.results))
            .catch((error) => setError(error));

        // fetch("https://movie-database-imdb-alternative.p.rapidapi.com/?s=Avengers%20Endgame&page=1&r=json", {
        //     "method": "GET",
        //     "headers": {
        //         "x-rapidapi-host": "movie-database-imdb-alternative.p.rapidapi.com",
        //         "x-rapidapi-key": "56b2bfe2d2msh4b8c5c88e7fc2a9p1f8de3jsn0fcb9aa6aa3b"
        //     }
        // })
        //     .then(response => {
        //         console.log(response);
        //     })
        //     .catch(err => {
        //         console.error(err);
        //     });

    }, []);

    console.log(error);

    return (
        <div>
            <div>
                {movieList.length > 0 ? (
                    <div className="flex-wrapper movie-overview">
                        {movieList.map((movie) => (
                            <Movie key={movie.title}
                                title={movie.title}
                                relaseDate={movie.release_date}
                                image={
                                    "https://www.themoviedb.org/t/p/w300_and_h450_bestv2/" +
                                    movie.poster_path
                                }
                                overview={movie.overview}
                            />
                        ))}
                    </div>
                ) : (
                    <h1>Loading Movies</h1>
                )}
            </div>
        </div>
    )
}

export default Movies;