import '../App.css'

const Movie = ({ title, relaseDate, image, overview }) => {
    return (
        <div className=" movie-img">
            <h3 className='movie-title'><span className='movie-title-span'>{title}</span> <br></br> <span className='movie-date-span'>{relaseDate}</span></h3>
            <img src={image} alt="Movie From TMDB" className='movie-img' />
            <div className='overview-container'>
                <p>{overview}</p>
            </div>
        </div>
    )
}
export default Movie